/* https://developer.mozilla.org/en-US/docs/Web/CSS/CSS_Container_Queries */
/* https://stackoverflow.com/questions/16056591/font-scaling-based-on-size-of-container/74977615#74977615 */
.fc {
  container-type: size;
  /* inline-size (only with); */ }

.fc table {
  font-size: 0.9cqw; }

/* DayGridView
--------------------------------------------------------------------------------------------------*/
/* day row structure */
.fc-year-container {
  /* a "rigid" row will take up a constant amount of height because content-skeleton is absolute */
  /* week and day number styling */
  /* when week/day number have own column */
  /* Add.Hiedra. .fc-row .fc-highlight-skeleton. The selection must be moved by the width of the "month name" column. */
  /* Global Event Styles
  --------------------------------------------------------------------------------------------------*/
  /* hit area */
  /* DayGrid events
  ----------------------------------------------------------------------------------------------------
  We use the full "fc-day-grid-event" class instead of using descendants because the event won't
  be a descendant of the grid when it is being dragged.
  */ }
  .fc-year-container .fc-dayGridWeek-view .fc-content-skeleton,
  .fc-year-container .fc-dayGridDay-view .fc-content-skeleton {
    /* there may be week numbers in these views, so no padding-top */
    padding-bottom: 1em;
    /* ensure a space at bottom of cell for user selecting/clicking */ }
  .fc-year-container .fc-dayGrid-view .fc-body .fc-row {
    min-height: 1em;
    /* ensure that all rows are at least this tall */ }
  .fc-year-container .fc-row.fc-rigid {
    overflow: hidden; }
  .fc-year-container .fc-row.fc-rigid .fc-content-skeleton {
    position: absolute;
    top: 0;
    left: 1.2cqw;
    right: 0; }
  .fc-year-container .fc-day-top.fc-other-month {
    opacity: 0.3; }
  .fc-year-container .fc-dayGrid-view .fc-week-number,
  .fc-year-container .fc-dayGrid-view .fc-day-number {
    padding: 2px; }
  .fc-year-container .fc-dayGrid-view th.fc-week-number,
  .fc-year-container .fc-dayGrid-view th.fc-day-number {
    padding: 0 2px;
    /* column headers can't have as much v space */
    /* font-size: 0.75em; */ }
  .fc-year-container .fc-ltr .fc-dayGrid-view .fc-day-top .fc-day-number {
    float: right; }
  .fc-year-container .fc-rtl .fc-dayGrid-view .fc-day-top .fc-day-number {
    float: left; }
  .fc-year-container .fc-ltr .fc-dayGrid-view .fc-day-top .fc-week-number {
    float: left;
    border-radius: 0 0 3px 0; }
  .fc-year-container .fc-rtl .fc-dayGrid-view .fc-day-top .fc-week-number {
    float: right;
    border-radius: 0 0 0 3px; }
  .fc-year-container .fc-dayGrid-view .fc-day-top .fc-week-number {
    min-width: 1.5em;
    text-align: center;
    background-color: #f2f2f2;
    color: #808080; }
  .fc-year-container .fc-dayGrid-view td.fc-week-number {
    text-align: center; }
  .fc-year-container .fc-dayGrid-view td.fc-week-number > * {
    /* work around the way we do column resizing and ensure a minimum width */
    display: inline-block;
    min-width: 1.2em; }
  .fc-year-container .fc-row-monthname {
    height: 100%;
    width: 1.2cqw;
    /* 1.25em; */
    position: absolute;
    text-align: center;
    border-right-width: 1px;
    border-right-style: solid;
    border-bottom-width: 1px;
    border-bottom-style: solid;
    font-size: 1cqw; }
    .fc-year-container .fc-row-monthname span {
      display: block;
      transform: rotate(270deg);
      position: relative;
      top: 50%; }
  .fc-year-container .fc-bg, .fc-year-container .fc-content-skeleton {
    left: 1.2cqw;
    /* 1.25em */ }
  .fc-year-container .fc-row .fc-highlight-skeleton {
    left: 1.2cqw;
    /* 1.25em */ }
  .fc-year-container .fc-day-number-weekday {
    padding-right: .3em;
    color: #aaa; }
  .fc-year-container .fc-sat,
  .fc-year-container .fc-sun {
    background-color: #f5f1bc; }
  .fc-year-container .fc-event {
    font-size: 1cqw;
    line-height: 2cqw;
    background-color: transparent !important;
    border-radius: 0;
    border: 0px;
    color: black;
    min-height: 2cqw;
    margin: 0; }
  .fc-year-container .fc-row .fc-content-skeleton {
    padding-bottom: 0; }
  .fc-year-container .fc-event.fc-selected .fc-resizer:before {
    /* 40x40 touch area */
    content: "";
    position: absolute;
    z-index: 9999;
    background-color: transparent !important;
    /* user of this util can scope within a lower z-index */
    /* top: 50%;
    left: 50%;
    width: 40px;
    height: 40px;
    margin-left: -20px;
    margin-top: -20px; */ }
  .fc-year-container .fc-day-grid-event {
    margin: 1px 2px 0;
    /* spacing between events and edges */
    padding: 0 1px; }
  .fc-year-container tr:first-child > td > .fc-day-grid-event {
    margin-top: 0;
    /* a little bit more space before the first event */ }
  .fc-year-container .fc-mirror-skeleton tr:first-child > td > .fc-day-grid-event {
    margin-top: 0;
    /* except for mirror skeleton */ }
  .fc-year-container .fc-row table {
    /* don't put left/right border on anything within a fake row.
       the outer tbody will worry about this */
    border-left: 0 hidden transparent;
    border-right: 0 hidden transparent;
    /* no bottom borders on rows */
    border-bottom: 0 hidden transparent; }
  .fc-year-container .fc-row .fc-content-skeleton tbody td,
  .fc-year-container .fc-row .fc-mirror-skeleton tbody td {
    /* don't put a border between event cells */
    border-top: 0;
    text-align: center; }
  .fc-year-container .fc-bgevent-skeleton,
  .fc-year-container .fc-highlight-skeleton,
  .fc-year-container .fc-mirror-skeleton {
    left: 1.2cqw; }
